/*Boxed styles for all color schemes*/

.stm-shop-sidebar .stm-fullwidth-row-js,
.stm-boxed .stm-fullwidth-row-js {
  overflow: hidden;

  margin: 0 -15px !important;
}

.stm-shop-sidebar .woocommerce-tabs,
.stm-boxed .woocommerce-tabs {
  margin: 60px 0 !important;
  padding-right: 15px;
  padding-left: 15px;
}

.stm-header .stm-main-menu .stm-main-menu-unit.stm-search-enabled .stm-header-search .search-submit {
  min-height: 70px;
}

.stm_post_comments .comment .comment-respond {
  padding: 20px 30px 20px !important;
}

.stm-single-product-loop .stm-product-content-loop-inner .stm-product-content-image {
  display: inline-block;

  width: 100%;

  vertical-align: top;
  img {
    width: 100%;
  }
}

body.stm-boxed {
  background-position: 50% 50%;
}

.sb_instagram_error {
  margin-bottom: 15px !important;
}

.single-product .stm-badge-wrapper {
  display: inline-block;
}

.stm-header .stm-main-menu .stm-main-menu-unit .header-menu > li a {
  text-transform: none;
}

.stm-latest-results-units .stm-latest-results-info .stm-latest-result {
  &.stm-latest-result-right .stm-latest-result-win-label {
    float: left;

    margin: 0 5px 0 0 !important;
  }
  .stm-latest-result-win-label {
    float: right;

    margin-left: 5px !important;
  }
}

.stm-single-sp_team .sp-post-content {
  padding-left: 1px;
}

.stm-header .stm-header-background {
  opacity: .8 !important;
  background-position: 50% 100% !important;
}
