.select2-container {
  position: relative;

  display: inline-block;

  box-sizing: border-box;
  margin: 0;

  vertical-align: middle;
  .select2-selection--single {
    display: block;

    box-sizing: border-box;
    height: 28px;

    cursor: pointer;
    -webkit-user-select: none;
            user-select: none;
    .select2-selection__rendered {
      display: block;
      overflow: hidden;

      padding-right: 20px;
      padding-left: 8px;

      white-space: nowrap;
      text-overflow: ellipsis;
    }
    .select2-selection__clear {
      position: relative;
    }
  }
  &[dir='rtl'] .select2-selection--single .select2-selection__rendered {
    padding-right: 8px;
    padding-left: 20px;
  }
  .select2-selection--multiple {
    display: block;

    box-sizing: border-box;
    min-height: 32px;

    cursor: pointer;
    -webkit-user-select: none;
            user-select: none;
    .select2-selection__rendered {
      display: inline-block;
      overflow: hidden;

      padding-left: 8px;

      white-space: nowrap;
      text-overflow: ellipsis;
    }
  }
  .select2-search--inline {
    float: left;
    .select2-search__field {
      font-size: 100%;

      box-sizing: border-box;
      margin-top: 5px;
      padding: 0;

      border: none;
      &::-webkit-search-cancel-button {
        -webkit-appearance: none;
      }
    }
  }
}

.select2-dropdown {
  position: absolute;
  z-index: 1051;
  left: -100000px;

  display: block;

  box-sizing: border-box;
  width: 100%;

  border: 1px solid #aaa;
  border-radius: 4px;
  background-color: white;
}

.select2-results {
  display: block;
}

.select2-results__options {
  margin: 0;
  padding: 0;

  list-style: none;
}

.select2-results__option {
  padding: 6px;

  -webkit-user-select: none;
          user-select: none;
  &[aria-selected] {
    cursor: pointer;
  }
}

.select2-container--open {
  .select2-dropdown {
    left: 0;
  }
  .select2-dropdown--above {
    border-bottom: none;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
  }
  .select2-dropdown--below {
    border-top: none;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }
}

.select2-search--dropdown {
  display: block;

  padding: 4px;
  .select2-search__field {
    box-sizing: border-box;
    width: 100%;
    padding: 4px;
    &::-webkit-search-cancel-button {
      -webkit-appearance: none;
    }
  }
  &.select2-search--hide {
    display: none;
  }
}

.select2-close-mask {
  position: fixed;
  z-index: 99;
  top: 0;
  left: 0;

  display: block;

  width: auto;
  min-width: 100%;
  height: auto;
  min-height: 100%;
  margin: 0;
  padding: 0;

  opacity: 0;
  border: 0;
  background-color: #fff;

  filter: alpha(opacity = 0);
}

.select2-hidden-accessible {
  position: absolute !important;

  overflow: hidden !important;
  clip: rect(0 0 0 0) !important;

  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  padding: 0 !important;

  border: 0 !important;
}

.select2-container--default {
  .select2-selection--single {
    border: 1px solid #aaa;
    border-radius: 4px;
    background-color: #fff;
    .select2-selection__rendered {
      line-height: 28px;

      color: #444;
    }
    .select2-selection__clear {
      font-weight: bold;

      float: right;

      cursor: pointer;
    }
    .select2-selection__placeholder {
      color: #999;
    }
    .select2-selection__arrow {
      position: absolute;
      top: 1px;
      right: 1px;

      width: 20px;
      height: 26px;
      b {
        position: absolute;
        top: 50%;
        left: 50%;

        width: 0;
        height: 0;
        margin-top: -2px;
        margin-left: -4px;

        border-width: 5px 4px 0 4px;
        border-style: solid;
        border-color: #888 transparent transparent transparent;
      }
    }
  }
  &[dir='rtl'] .select2-selection--single {
    .select2-selection__clear {
      float: left;
    }
    .select2-selection__arrow {
      right: auto;
      left: 1px;
    }
  }
  &.select2-container--disabled .select2-selection--single {
    cursor: default;

    background-color: #eee;
    .select2-selection__clear {
      display: none;
    }
  }
  &.select2-container--open .select2-selection--single .select2-selection__arrow b {
    border-width: 0 4px 5px 4px;
    border-color: transparent transparent #888 transparent;
  }
  .select2-selection--multiple {
    cursor: text;

    border: 1px solid #aaa;
    border-radius: 4px;
    background-color: white;
    .select2-selection__rendered {
      box-sizing: border-box;
      width: 100%;
      margin: 0;
      padding: 0 5px;

      list-style: none;
    }
    .select2-selection__placeholder {
      float: left;

      margin-top: 5px;

      color: #999;
    }
    .select2-selection__clear {
      font-weight: bold;

      float: right;

      margin-top: 5px;
      margin-right: 10px;

      cursor: pointer;
    }
    .select2-selection__choice {
      float: left;

      margin-top: 5px;
      margin-right: 5px;
      padding: 0 5px;

      cursor: default;

      border: 1px solid #aaa;
      border-radius: 4px;
      background-color: #e4e4e4;
    }
    .select2-selection__choice__remove {
      font-weight: bold;

      display: inline-block;

      margin-right: 2px;

      cursor: pointer;

      color: #999;
      &:hover {
        color: #333;
      }
    }
  }
  &[dir='rtl'] .select2-selection--multiple {
    .select2-selection__choice,
    .select2-selection__placeholder,
    .select2-search--inline {
      float: right;
    }
    .select2-selection__choice {
      margin-right: auto;
      margin-left: 5px;
    }
    .select2-selection__choice__remove {
      margin-right: auto;
      margin-left: 2px;
    }
  }
  &.select2-container--focus .select2-selection--multiple {
    border: solid black 1px;
    outline: 0;
  }
  &.select2-container--disabled {
    .select2-selection--multiple {
      cursor: default;

      background-color: #eee;
    }
    .select2-selection__choice__remove {
      display: none;
    }
  }
  &.select2-container--open {
    &.select2-container--above {
      .select2-selection--single,
      .select2-selection--multiple {
        border-top-left-radius: 0;
        border-top-right-radius: 0;
      }
    }
    &.select2-container--below {
      .select2-selection--single,
      .select2-selection--multiple {
        border-bottom-right-radius: 0;
        border-bottom-left-radius: 0;
      }
    }
  }
  .select2-search--dropdown .select2-search__field {
    border: 1px solid #aaa;
  }
  .select2-search--inline .select2-search__field {
    border: none;
    outline: 0;
    background: transparent;
    box-shadow: none;

    -webkit-appearance: textfield;
  }
  .select2-results > .select2-results__options {
    overflow-y: auto;

    max-height: 200px;
  }
  .select2-results__option {
    &[role=group] {
      padding: 0;
    }
    &[aria-disabled=true] {
      color: #999;
    }
    &[aria-selected=true] {
      background-color: #ddd;
    }
    .select2-results__option {
      padding-left: 1em;
      .select2-results__group {
        padding-left: 0;
      }
      .select2-results__option {
        margin-left: -1em;
        padding-left: 2em;
        .select2-results__option {
          margin-left: -2em;
          padding-left: 3em;
          .select2-results__option {
            margin-left: -3em;
            padding-left: 4em;
            .select2-results__option {
              margin-left: -4em;
              padding-left: 5em;
              .select2-results__option {
                margin-left: -5em;
                padding-left: 6em;
              }
            }
          }
        }
      }
    }
  }
  .select2-results__option--highlighted[aria-selected] {
    color: white;
    background-color: #5897fb;
  }
  .select2-results__group {
    display: block;

    padding: 6px;

    cursor: default;
  }
}

.select2-container--classic {
  .select2-selection--single {
    border: 1px solid #aaa;
    border-radius: 4px;
    outline: 0;
    background-color: #f7f7f7;
    background-image: -webkit-linear-gradient(top, #fff 50%, #eee 100%);
    background-image:      -o-linear-gradient(top, #fff 50%, #eee 100%);
    background-image:         linear-gradient(to bottom, #fff 50%, #eee 100%);
    background-repeat: repeat-x;

    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#FFFFFFFF', endColorstr='#FFEEEEEE', GradientType=0);
    &:focus {
      border: 1px solid #5897fb;
    }
    .select2-selection__rendered {
      line-height: 28px;

      color: #444;
    }
    .select2-selection__clear {
      font-weight: bold;

      float: right;

      margin-right: 10px;

      cursor: pointer;
    }
    .select2-selection__placeholder {
      color: #999;
    }
    .select2-selection__arrow {
      position: absolute;
      top: 1px;
      right: 1px;

      width: 20px;
      height: 26px;

      border: none;
      border-left: 1px solid #aaa;
      border-top-right-radius: 4px;
      border-bottom-right-radius: 4px;
      background-color: #ddd;
      background-image: -webkit-linear-gradient(top, #eee 50%, #ccc 100%);
      background-image:      -o-linear-gradient(top, #eee 50%, #ccc 100%);
      background-image:         linear-gradient(to bottom, #eee 50%, #ccc 100%);
      background-repeat: repeat-x;

      filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#FFEEEEEE', endColorstr='#FFCCCCCC', GradientType=0);
      b {
        position: absolute;
        top: 50%;
        left: 50%;

        width: 0;
        height: 0;
        margin-top: -2px;
        margin-left: -4px;

        border-width: 5px 4px 0 4px;
        border-style: solid;
        border-color: #888 transparent transparent transparent;
      }
    }
  }
  &[dir='rtl'] .select2-selection--single {
    .select2-selection__clear {
      float: left;
    }
    .select2-selection__arrow {
      right: auto;
      left: 1px;

      border: none;
      border-right: 1px solid #aaa;
      border-radius: 0;
      border-top-left-radius: 4px;
      border-bottom-left-radius: 4px;
    }
  }
  &.select2-container--open {
    .select2-selection--single {
      border: 1px solid #5897fb;
      .select2-selection__arrow {
        border: none;
        background: transparent;
        b {
          border-width: 0 4px 5px 4px;
          border-color: transparent transparent #888 transparent;
        }
      }
    }
    &.select2-container--above .select2-selection--single {
      border-top: none;
      border-top-left-radius: 0;
      border-top-right-radius: 0;
      background-image: -webkit-linear-gradient(top, #fff 0%, #eee 50%);
      background-image:      -o-linear-gradient(top, #fff 0%, #eee 50%);
      background-image:         linear-gradient(to bottom, #fff 0%, #eee 50%);
      background-repeat: repeat-x;

      filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#FFFFFFFF', endColorstr='#FFEEEEEE', GradientType=0);
    }
    &.select2-container--below .select2-selection--single {
      border-bottom: none;
      border-bottom-right-radius: 0;
      border-bottom-left-radius: 0;
      background-image: -webkit-linear-gradient(top, #eee 50%, #fff 100%);
      background-image:      -o-linear-gradient(top, #eee 50%, #fff 100%);
      background-image:         linear-gradient(to bottom, #eee 50%, #fff 100%);
      background-repeat: repeat-x;

      filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#FFEEEEEE', endColorstr='#FFFFFFFF', GradientType=0);
    }
  }
  .select2-selection--multiple {
    cursor: text;

    border: 1px solid #aaa;
    border-radius: 4px;
    outline: 0;
    background-color: white;
    &:focus {
      border: 1px solid #5897fb;
    }
    .select2-selection__rendered {
      margin: 0;
      padding: 0 5px;

      list-style: none;
    }
    .select2-selection__clear {
      display: none;
    }
    .select2-selection__choice {
      float: left;

      margin-top: 5px;
      margin-right: 5px;
      padding: 0 5px;

      cursor: default;

      border: 1px solid #aaa;
      border-radius: 4px;
      background-color: #e4e4e4;
    }
    .select2-selection__choice__remove {
      font-weight: bold;

      display: inline-block;

      margin-right: 2px;

      cursor: pointer;

      color: #888;
      &:hover {
        color: #555;
      }
    }
  }
  &[dir='rtl'] .select2-selection--multiple {
    .select2-selection__choice {
      float: right;

      margin-right: auto;
      margin-left: 5px;
    }
    .select2-selection__choice__remove {
      margin-right: auto;
      margin-left: 2px;
    }
  }
  &.select2-container--open {
    .select2-selection--multiple {
      border: 1px solid #5897fb;
    }
    &.select2-container--above .select2-selection--multiple {
      border-top: none;
      border-top-left-radius: 0;
      border-top-right-radius: 0;
    }
    &.select2-container--below .select2-selection--multiple {
      border-bottom: none;
      border-bottom-right-radius: 0;
      border-bottom-left-radius: 0;
    }
  }
  .select2-search--dropdown .select2-search__field {
    border: 1px solid #aaa;
    outline: 0;
  }
  .select2-search--inline .select2-search__field {
    outline: 0;
    box-shadow: none;
  }
  .select2-dropdown {
    border: 1px solid transparent;
    background-color: #fff;
  }
  .select2-dropdown--above {
    border-bottom: none;
  }
  .select2-dropdown--below {
    border-top: none;
  }
  .select2-results > .select2-results__options {
    overflow-y: auto;

    max-height: 200px;
  }
  .select2-results__option {
    &[role=group] {
      padding: 0;
    }
    &[aria-disabled=true] {
      color: grey;
    }
  }
  .select2-results__option--highlighted[aria-selected] {
    color: #fff;
    background-color: #3875d7;
  }
  .select2-results__group {
    display: block;

    padding: 6px;

    cursor: default;
  }
  &.select2-container--open .select2-dropdown {
    border-color: #5897fb;
  }
}
